import '@servicenow/now-card';
import { t } from 'sn-translate';

const view = (state) => {
	const {titleLabel, detailLabelOne, detailValueOne, detailLabelTwo, detailValueTwo, detailLabelThree, detailValueThree, model, clickAction, dueDate} = state.properties;
	const labelValueItems = [
	{
		label: detailLabelOne,
		value: {
			type: 'string',
			value: detailValueOne
		}
	},
	{
		label: detailLabelTwo,
		value: {
			type: 'string',
			value: detailValueTwo
		}
	},
	{
		label: detailLabelThree,
		value: {
			type: 'string',
			value: detailValueThree
		}
	}
];
const payload = { action: clickAction, model: model }
	return (
		<now-card size="md" interaction="click" append-to-payload={payload}>
			<now-card-header heading={{label: titleLabel || t("Assessment"), size: 'sm', lines: 2}} />
			<now-label-value-stacked
					truncated
					wrapped
					align="horizontal"
					delimiter=","
					items={labelValueItems}
					size="md"
				/>
			<now-card-footer label={t(`Due Date: ${dueDate}`)} />
		</now-card>
	);
};

export default view;
