import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import styles from './styles.scss';
import {DECLARATIVE_ACTION, NOW_CARD_CLICKED} from '../constants';

createCustomElement('sn-coaching-evam-assessment-card', {
  renderer: {type: snabbdom},
	properties: {
		/**
		* Defines the main title of the card.
		* @type {string}
		*/
		titleLabel: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines a key displayed in the secondary information section.
		* @type {string}
		*/
		detailLabelOne: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines a value displayed in the secondary information section.
		* @type {string}
		*/
		detailValueOne: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines a key displayed in the secondary information section.
		* @type {string}
		*/
		detailLabelTwo: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines a value displayed in the secondary information section.
		* @type {string}
		*/
		detailValueTwo: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines a key displayed in the secondary information section.
		* @type {string}
		*/
		detailLabelThree: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines a value displayed in the secondary information section.
		* @type {string}
		*/
		detailValueThree: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines a value displayed in the secondary information section.
		* @type {string}
		*/
		dueDate: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines the action metadata when the card is clicked.
		* @type {Object}
		*/
		clickAction: {
			default: {},
			schema: {
				type: 'object'
			}
		},
		/**
		* Placeholder property defining the declarative action model provided by the EVAM infrastructure.
		* @type {Object}
		*/
		model: {
			default: {},
			schema: {
				type: 'object'
			}
		}
	},
  view,
	styles,
	actionHandlers: {
		[NOW_CARD_CLICKED]: ({ dispatch, action: { payload: { action, model } } }) => {
			dispatch(DECLARATIVE_ACTION, { action, model });
		}
	},
	dispatches: {
		/**
		* @private
		*/
		'DECLARATIVE_ACTION': {}
	}
});