import '@servicenow/now-avatar';

const view = (state) => {
	const { section, dir } = state.properties;
	const titlePadding = 42;
	const namePadding = 8;
	let titleStyle = {
		padding: `0px 0px 0px ${titlePadding}px`,
	};
	let nameStyle = {
		padding: `0px 0px 0px ${namePadding}px`,
	};

	if (dir === "rtl") {
		titleStyle = {
			padding: `0px ${titlePadding}px 0px 0px`,
		};
		nameStyle = {
			padding: `0px ${namePadding}px 0px 0px`,
		};
	}
	return (
		<span attrs={{ title: section.title }} style={titleStyle}>
			<now-avatar
				aria-hidden="true"
				size="sm"
				userName={section.title}
			></now-avatar>
			<span style={nameStyle} className="user-name">{section.title}</span>
		</span>
	);
};

export default view;
