import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import styles from './styles.scss';
import moment from 'moment';
import {INTERNAL_FORMAT_DATE_TIME, SN_SHIFT_PALNNING_TIME_SCALE_GRID_DATE_CLICKED} from '../constants';

createCustomElement('sn-shift-planning-time-scale-grid', {
	renderer: {type: snabbdom},
	properties: {
		calendarProperties: {
			default: {}
		},
		viewConfig: {
			default: {}
		},
		timezone: {
			default: {}
		},
		sections: {
			default: {}
		},
		contextDate: {
			default: {}
		},
		events: {
			default: {}
		},
		timeFormat: {
			default: ''
		},
		dateFormat: {
			default: ''
		},
		locale: {
			default: ''
		}
	},
	view,
	styles,
	initialState: {
		contextMoment: {},
		todayMoment: moment().format(INTERNAL_FORMAT_DATE_TIME)
	},
	actionHandlers: {
		[SN_SHIFT_PALNNING_TIME_SCALE_GRID_DATE_CLICKED]: ({action, updateState}) => {
			updateState({
				path: 'contextMoment',
				operation: 'set',
				value: action.payload
			});
		}
	}
});