import '@servicenow/now-icon';
import { t } from 'sn-translate';

const view = (state) => {
	const {section, dir, isCollapsed} = state.properties;
	section.isCollapsed = false;
	section.level = 0;

	const ARIA_LABEL_COLLAPSED = t(`Collapsed, click to expand ${section.title}`);
	const ARIA_LABEL_EXPANDED = t(`Expanded, click to collapse ${section.title}`);
	let ariaLabel = ARIA_LABEL_EXPANDED;

	let iconName = 'chevron-down-outline';
	if (isCollapsed) {
		ariaLabel = ARIA_LABEL_COLLAPSED;
		iconName = 'chevron-right-outline';
		if (dir === 'rtl') {
			iconName = 'chevron-left-outline';
		}
	}

	return (
		<div>
			<span section_id={section.id}>
				<now-icon
					icon={iconName}
					size="md"
					className="row-header-icon clickable"
					attrs={{ section_id: section.id }}
				>
					{' '}
				</now-icon>
			</span>
			<span
				title={section.title}
				aria-label={ariaLabel}
				className="row-header-title"
			>
				{section.title}
			</span>
		</div>
	);
};

export default view;