import moment from 'moment';
import { TimelineUtils } from '@servicenow/now-calendar';
import isEmpty from 'lodash/isEmpty';
import { getCoverage } from '../utils';
import {INTERNAL_FORMAT_DATE_TIME, SN_SHIFT_PALNNING_TIME_SCALE_GRID_DATE_CLICKED, CALENDAR_VIEWS_TIMELINE_WEEK, CALENDAR_VIEWS_TIMELINE_DAY} from '../constants';

const getHeaderTimeCellView = (state, dispatch, startTime, updateState) => {
	const {
		viewConfig,
		sections,
		timezone,
		events,
		timeFormat,
		locale
	} = state.properties;
	const contextDate = isEmpty(state.properties.contextDate) ? moment().format(INTERNAL_FORMAT_DATE_TIME) : state.properties.contextDate;
	const aggregatorStyle = {
		'background-color': 'RGB(var(--now-color--neutral-2,228,230,231))',
		height: '40px',
		display: 'flex',
		'align-items': 'center',
		'justify-content': 'center',
		'font-size': '12px',
		'border-top': '1px solid rgb(var(--now-color--neutral-5,172,180,181))',
		'margin-bottom': '-0.3rem'
	};
	const coverageIndex =
		viewConfig.viewName === CALENDAR_VIEWS_TIMELINE_DAY ? startTime.hour() : startTime.day();
	let coverageSum = 0;
	let cellLabel;

	if (viewConfig.showCoverageRow) {
		sections.forEach((section) => {
			let coverageArray = section.runtimeData.get(
				'coverage-' + viewConfig.viewName
			);
			if (!coverageArray || coverageArray[coverageIndex] == null) {
				coverageArray = getCoverage(viewConfig, events, contextDate, sections, timezone, section);
			}
			coverageSum += coverageArray[coverageIndex];
		});

		cellLabel = coverageSum;
	}

	const timeFormatStyle = {
		'line-height': '16px',
		'font-size': '0.75rem',
		'text-align': 'center',
		padding: '0px 0px 5px',
		color:
			'RGB(var(--now-color_text--primary,var(--now-color--neutral-18,22,27,28)))',
		'background-color': 'rgb(255, 255, 255)'
	};
	const contextualPanelCurrentView = '';

	if (viewConfig.viewName === CALENDAR_VIEWS_TIMELINE_WEEK) {
		const dateCls = ['date-number', 'clickable'];
		const onDateClick = /**
		 * @param {any} event
		 * @param {moment.Moment} date
		 */ (event, date) => {
				if (contextualPanelCurrentView !== 'agenda-view')
					dispatch('NOW_CALENDAR#INTERNAL_STATE_SET', {
						contextualPanelCurrentView: 'agenda-view'
					});
				dispatch(SN_SHIFT_PALNNING_TIME_SCALE_GRID_DATE_CLICKED, date);
			};
		aggregatorStyle['margin-bottom'] = '-0.4rem';
		aggregatorStyle['margin-top'] = '0.2rem';

		return (
			<div className="tableClass">
				<div className="day">{startTime.locale(locale).format('dddd')}</div>
				<div
					className={dateCls.join(' ')}
					tabindex="-1"
					on-click={(event) => {
						onDateClick(event, startTime, updateState);
					}}
					aria-label={startTime.locale(locale).format('dddd, MMMM Do YYYY')}
				>
					{startTime.locale(locale).format('DD')}
				</div>
				{viewConfig.showCoverageRow ? (
					<div style={aggregatorStyle}>
						{cellLabel}
					</div>
				) : null}
			</div>
		);
	}

	return (
		<div>
			<div style={timeFormatStyle}>{startTime.format(timeFormat)}</div>
			{viewConfig.showCoverageRow ? (
				<div style={aggregatorStyle}>
					{cellLabel}
				</div>
			) : null}
		</div>
	);
};

const view = (state, dispatch, updateState) => {
	const { viewConfig } = state.properties;
	const timeScaleGridStyle = {
		width: TimelineUtils.getEventLineWidth(viewConfig) + 'px'
	};
	const tableStyle = {
		tableLayout: 'fixed',
		...timeScaleGridStyle,
		transform: ''
	};
	tableStyle.width = `${TimelineUtils.getEventLineWidth(viewConfig)}px`;

	const contextDay = isEmpty(state.properties.contextDate) ?  moment() : moment(state.properties.contextDate);
	const startTime = contextDay.startOf('week');
	let dates = TimelineUtils.getRenderViewDates(startTime, viewConfig);
	const cellWidth = '69';
	let uiCellWidth = `${cellWidth}px`;
	let cellViews = dates.map((thisDate) => {
		const style = {
			width: uiCellWidth,
			minWidth: uiCellWidth,
			maxWidth: uiCellWidth,
			'border-right': '1px solid rgb(var(--now-color--neutral-5,172,180,181))',
			padding: '5px 0px'
		};
		const { contextMoment, todayMoment } = state;
		let cls = [];
		if (
			moment(thisDate.start)
				.startOf('day')
				.isSame(moment(todayMoment).startOf('day'))
		)
			cls.push('today');
		if (
			moment(thisDate.start)
				.startOf('day')
				.isSame(moment(contextMoment).startOf('day'))
		)
			cls.push('contextDate');
		return (
			<td style={style} className={cls.join(' ')}>
				{getHeaderTimeCellView(state, dispatch, thisDate.start, updateState)}
			</td>
		);
	});

	return (
		<table
			role="presentation"
			style={tableStyle}
			className="timeline-header-slots"
			cellSpacing="0"
			cellPadding="0"
		>
			<tr>{cellViews}</tr>
		</table>
	);
};

export default view;
