import moment from 'moment-timezone';
import get from 'lodash/get';
import uniq from 'lodash/uniq';
import difference from 'lodash/difference';
import {
  CALENDAR_VIEWS_TIMELINE_WEEK,
	INTERNAL_FORMAT_DATE_TIME,
	CALENDAR_EVENTS_WORK_SHIT_CATEGORY,
} from './constants';
export const getCoverage = (
	viewConfig,
	events,
	contextDate,
	allSections,
	timezone,
	section,
	preciseTime,
	numberOfSections
) => {
	let { xSize, xStep, xUnitName, viewName } = viewConfig;

	if (numberOfSections) xSize = numberOfSections;

	//Coverage array will hold list of agents that are available for each interval of time axis, in order.
	//These will eventually be converted into counts.
	let coverage = [];

	//If a specific section was provided, take a list of all users in that section
	let groupIDs = [];
	for (let i in allSections) {
		let s = allSections[i];
		if (!section || s.title == section.title) {
			let ch = s.children;
			for (let j in ch) groupIDs.push(ch[j].id);
		}
	}
	for (let i = 0; i < xSize; i++) {
		coverage.push({
			//These fields determine which users are active and which users aren't
			at_work: [],
			excluded: [],
			//These fields are only for calculating the two above in week view
			work_intervals: {},
			exclude_intervals: {}
		});
	}

	for (let i in events) {
		//Only process events that are for users in this section
		let ev = events[i];
		if (section && !groupIDs.includes(ev.group)) continue;

		//Get information on event start/end time, as well as info on its children
		let user = ev.group;

		let eventStart = moment.utc(
			moment
				.tz(ev.start, INTERNAL_FORMAT_DATE_TIME, ev.originalReadInTimezone)
				.tz(timezone)
				.format(INTERNAL_FORMAT_DATE_TIME),
			INTERNAL_FORMAT_DATE_TIME
		);

		let eventEnd = moment.utc(
			moment
				.tz(ev.end, INTERNAL_FORMAT_DATE_TIME, ev.originalReadInTimezone)
				.tz(timezone)
				.format(INTERNAL_FORMAT_DATE_TIME),
			INTERNAL_FORMAT_DATE_TIME
		);

		let childEventData = [];

		for (let j in ev.childEvents) {
			let child = ev.childEvents[j];
			let childData = {};

			childData.start = moment.utc(child.start, INTERNAL_FORMAT_DATE_TIME);

			childData.end = moment.utc(child.end, INTERNAL_FORMAT_DATE_TIME);

			childData.isExclusion = child.isExclusion;
			childEventData.push(childData);
		}

		let intervalStart = moment.utc(contextDate, INTERNAL_FORMAT_DATE_TIME);

		if (!preciseTime) {
			//If we don't need to keep time component of contextDate:
			intervalStart.startOf('day'); //Set time to start of day
			if (viewName === CALENDAR_VIEWS_TIMELINE_WEEK) intervalStart.day(0); //Set interval start date to Sunday
		}

		let intervalEnd = moment
			.utc(intervalStart, INTERNAL_FORMAT_DATE_TIME)
			.add(xStep, xUnitName);

		//For each interval on time axis, add to coverage as necessary, depending on if event indicates
		//a user is available/unavailable for any point in that interval
		for (let k = 0; k < xSize; k++) {
			if (eventStart.isBefore(intervalEnd) && eventEnd.isAfter(intervalStart)) {
				//For calculating work view, we only want to consider exclusion events if they overlap with a work shift
				//so store the data to be readily available for now and this will be calculated later
				if (viewName === CALENDAR_VIEWS_TIMELINE_WEEK) {
					if (ev.isExclusion) {
						if (!coverage[k].exclude_intervals[user])
							coverage[k].exclude_intervals[user] = [];

						coverage[k].exclude_intervals[user].push({
							start: eventStart.format(INTERNAL_FORMAT_DATE_TIME),
							end: eventEnd.format(INTERNAL_FORMAT_DATE_TIME)
						});
					} else {
						if (
							get(ev, 'schedule.eventType.category.sys_id', '') ===
							CALENDAR_EVENTS_WORK_SHIT_CATEGORY
						) {
							coverage[k].at_work.push(user);

							if (!coverage[k].work_intervals[user])
								coverage[k].work_intervals[user] = [];

							coverage[k].work_intervals[user].push({
								start: eventStart.format(INTERNAL_FORMAT_DATE_TIME),
								end: eventEnd.format(INTERNAL_FORMAT_DATE_TIME)
							});
						}
					}
				} else {
					if (ev.isExclusion) coverage[k].excluded.push(user);
					else coverage[k].at_work.push(user);
				}
			}

			if (viewName !== CALENDAR_VIEWS_TIMELINE_WEEK) {
				childEventData.forEach((child) => {
					if (
						child.start.isSameOrBefore(intervalStart) &&
						child.end.isSameOrAfter(intervalEnd) &&
						child.isExclusion
					)
						coverage[k].excluded.push(ev.group);
				});
			}

			intervalStart.add(xStep, xUnitName);
			intervalEnd.add(xStep, xUnitName);
		}
	}

	//If in week view, only consider an exclusion if it overlaps with an agent's work shift
	if (viewName === CALENDAR_VIEWS_TIMELINE_WEEK) {
		coverage.forEach((intervalData, interval) => {
			for (let [agent, agentWorkShifts] of Object.entries(
				intervalData.work_intervals
			)) {
				agentWorkShifts.forEach((workData) => {
					let agentExclusions = intervalData.exclude_intervals[agent];
					if (agentExclusions) {
						agentExclusions.forEach((excludeData) => {
							if (
								workData.start < excludeData.end &&
								workData.end > excludeData.start
							)
								coverage[interval].excluded.push(agent);
						});
					}
				});
			}
		});
	}

	//Convert lists into counts of unique users, subtracting list of excluded users from list of working users
	coverage = coverage.map(
		(x) => difference(uniq(x.at_work), uniq(x.excluded)).length
	);

	return coverage;
};