import { CalendarEvent, TimelineEvent } from '@servicenow/now-calendar';
import {
	CALENDAR_EVENTS_WORK_SHIT_CATEGORY,
	CALENDAR_VIEWS_TIMELINE_WEEK
} from '../constants';
import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';
import moment from 'moment';
import { t } from 'sn-translate';

const _getEventBodyTitle = (rawEvent, ignoreIfWorkEvent) => {
	if (!rawEvent.isPublished) return t('Draft');
	else if (
		ignoreIfWorkEvent &&
		get(rawEvent, 'schedule.eventType.category.sys_id', '') ===
			CALENDAR_EVENTS_WORK_SHIT_CATEGORY
	)
		return '';
	else if (get(rawEvent, 'schedule.workSchedule.name', '') !== '')
		return rawEvent.schedule.workSchedule.name;
	else return rawEvent.title;
};

const layout = (
	viewConfig,
	timelineEvent,
	parentEvent,
	tzViewStartMS,
	tzViewEndMS
) => {
	const CHILD_EVENT_TOP_PADDING = 2;
	const timezoneOffset = parentEvent.rawEvent.startUTCOffsetMS;

	let parentEventStartMS = parentEvent.rawEvent.startMS + timezoneOffset;
	let parentEventEndMS = parentEvent.rawEvent.endMS + timezoneOffset;

	let tzEventStartMS = timelineEvent.rawEvent.startMS + timezoneOffset;
	let tzEventEndMS = timelineEvent.rawEvent.endMS + timezoneOffset;

	// Check the boundary condition for parent event
	parentEventStartMS =
		parentEventStartMS < tzViewStartMS ? tzViewStartMS : parentEventStartMS;
	parentEventEndMS =
		parentEventEndMS > tzViewEndMS ? tzViewEndMS : parentEventEndMS;

	if (tzEventStartMS > parentEventEndMS || tzEventEndMS < parentEventStartMS) {
		timelineEvent.isOutOfView = true;
		return;
	}

	// Check the boundary condition for child event
	tzEventStartMS =
		tzEventStartMS < parentEventStartMS ? parentEventStartMS : tzEventStartMS;
	tzEventEndMS =
		tzEventEndMS > parentEventEndMS ? parentEventEndMS : tzEventEndMS;

	const posX =
		((tzEventStartMS - parentEventStartMS) * 100) /
		(parentEventEndMS - parentEventStartMS);
	const width =
		((tzEventEndMS - tzEventStartMS) * 100) /
		(parentEventEndMS - parentEventStartMS);

	timelineEvent.x = posX;
	timelineEvent.y = 0 + CHILD_EVENT_TOP_PADDING;
	timelineEvent.width = width;
	timelineEvent.height = viewConfig.eventHeight - CHILD_EVENT_TOP_PADDING;
};

const view = (state) => {
	const {
		viewConfig,
		event,
		timezone,
		dir,
		contextDate
	} = state.properties;
	var newCalendarEvent = new CalendarEvent(event, timezone);
	newCalendarEvent.setTimezone(timezone);
	const viewStartMoment = moment(contextDate).startOf('day');
	const viewEndMoment = moment(contextDate).endOf('day');
	const viewStartMS = viewStartMoment.valueOf();
	const viewEndMS = viewEndMoment.valueOf();

	const eventStartMS =
		newCalendarEvent.startMS + newCalendarEvent.startUTCOffsetMS;
	const eventEndMS = newCalendarEvent.endMS + newCalendarEvent.endUTCOffsetMS;
	const timelineEvent = new TimelineEvent(
		newCalendarEvent.id,
		newCalendarEvent.title,
		eventStartMS < state.tzViewStartMS,
		eventEndMS > state.tzViewEndMS,
		newCalendarEvent[viewConfig.groupBy],
		newCalendarEvent
	);
	let bodyContainer = {
		height: '18px',
		width: '100%',
		backgroundColor: newCalendarEvent.bgColor
	};

	if (viewConfig.viewName === CALENDAR_VIEWS_TIMELINE_WEEK) {
		bodyContainer.padding = '2px 0 0 5px';
		return (
			<div style={bodyContainer}>
				<span className="event-title">{_getEventBodyTitle(timelineEvent.rawEvent)}</span>
			</div>
		);
	}

	const leftRightOffset = dir === 'rtl' ? 'right' : 'left';
	let childEvents = [];

	if (isEmpty(timelineEvent.rawEvent.childEvents) === false) {
		childEvents = timelineEvent.rawEvent.childEvents.map(
			(childEvent, index) => {
				let calEvent = new CalendarEvent(childEvent, timezone);
				let timelineEv = new TimelineEvent(
					timelineEvent.id + '_child_' + index,
					calEvent.title,
					false,
					false,
					null,
					calEvent
				);

				layout(viewConfig, timelineEv, timelineEvent, viewStartMS, viewEndMS);

				if (timelineEv.isOutOfView) return '';

				let bgColor = calEvent.bgColor ? calEvent.bgColor : '#CCC';

				let styles = {
					top: '0',
					width: timelineEv.width + '%',
					[leftRightOffset]: timelineEv.x + '%',
					height: timelineEv.height + 'px',
					position: 'absolute',
					backgroundColor: bgColor,
					fontSize: '12px',
					overflow: 'hidden',
					textOverflow: 'ellipsis',
					whiteSpace: 'nowrap',
					maxWidth: '100%'
				};
				return <div style={styles} key={timelineEv.id} />;
			}
		);
	}

	return (
		<div style={bodyContainer} key={timelineEvent.id}>
			<div className="child-events">{childEvents}</div>
			<div className="event-title">
				{_getEventBodyTitle(timelineEvent.rawEvent, true)}
			</div>
		</div>
	);
};

export default view;
