import {
	CALENDAR_VIEWS_TIMELINE_WEEK
} from '../constants';
import moment from 'moment';
import {getCoverage} from '../utils';
import isEmpty from 'lodash/isEmpty';
import {INTERNAL_FORMAT_DATE_TIME} from '../constants';

const view = (state) => {
	const {
		viewConfig,
		section,
		events,
		timezone,
		sections
	} = state.properties;
	let cellwidth;

	if (viewConfig.noScrolling) {
		cellwidth = 100 / viewConfig.xSize + '%';
	} else {
		cellwidth = 70;
		if (viewConfig.viewName === CALENDAR_VIEWS_TIMELINE_WEEK) cellwidth = 240;
		if (viewConfig.xSize * cellwidth < viewConfig.eventAreaClientWidth)
			cellwidth = Math.floor(
				viewConfig.eventAreaClientWidth / viewConfig.xSize
			);

		cellwidth = cellwidth + 'px';
	}

	const tableCellStyle = {
		width: cellwidth,
		'min-width': cellwidth,
		'max-width': cellwidth
	};

	const contextDate = isEmpty(state.properties.contextDate) ? moment().format(INTERNAL_FORMAT_DATE_TIME) : state.properties.contextDate;

	const coverageByViewKey = 'coverage-' + viewConfig.viewName;
	sections.forEach((s) => {
		const { runtimeData } = s;
		if (runtimeData && !runtimeData.has(coverageByViewKey)) {
			runtimeData.set(
				coverageByViewKey,
				getCoverage(viewConfig, events, contextDate, sections, timezone, s)
			);
		}
	});

	let coverage = section.runtimeData
		? section.runtimeData.get(coverageByViewKey)
		: 0;

	if (!coverage) {
		return null;
	}

	return (
		<div className="tableParentStyle">
			<table className="tableStyle">
				<tr>
					{coverage.map((c) => (
						<td style={tableCellStyle} className="tableCellStyle">
							{c}
						</td>
					))}
				</tr>
			</table>
		</div>
	);
};

export default view;
